<template>
  <div>
    <el-button type="primary" @click="$router.push('admin/add')">
      添加
      <i class="el-icon-circle-plus-outline"></i>
    </el-button>
    <div class="table-bg">
      <el-table :data="list">
        <el-table-column width="350" label="管理UID" prop="uid"></el-table-column>
        <el-table-column label="管理名称" align="center" prop="username"></el-table-column>
        <el-table-column label="角色名称" align="center" prop="rolename">
          <template slot-scope="scope">
            <el-tag type="warning">{{scope.row.rolename}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="状态" align="center" prop="status">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.status==1" type="success">正常</el-tag>
            <el-tag v-else type="info">禁用</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="操作" prop="status" align="center">
          <template slot-scope="scope">
            <el-button
              @click="$router.push('/admin/edit/'+scope.row.uid)"
              icon="el-icon-edit"
              type="success"
              circle
              size="small"
            ></el-button>
            <el-button
              @click="del(scope.row.uid)"
              type="danger"
              icon="el-icon-delete"
              circle
              size="small"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <br>
      <el-pagination
        style="text-align:right;"
        background
        :page-size="size"
        :current-page.sync="pageIndex"
        @current-change="changePage"
        layout="prev, pager, next"
        :total="total">
        </el-pagination>
    </div>
  </div>
</template>

<script>
import { mapGetters,mapActions } from "vuex"
export default {
  data() {
    return {
      pageIndex:1
    };
  },
  computed: {
    ...mapGetters({
       total:"admin/total",
       list:"admin/adminlist",
       size:"admin/size",
       page:"admin/page"
    })
  },
  mounted() {
    if(!this.list.length){
      this.getlist();
    }else{
      this.pageIndex = this.page
    }
  },
  methods: {
    ...mapActions({
      getlist:"admin/getlist",
      set_page:"admin/set_page",
      del:"admin/del"
    }),
    changePage(val){ // val就是调整的页码数
       this.set_page(val)
    } 
  },
  components: {},
};
</script>
<style scoped>
</style>


